export default "#version 300 es\r\nprecision highp float;\r\n\r\nuniform float uTime;\r\nuniform float transX;\r\nuniform float transY;\r\nuniform float transZ;\r\nuniform float range;\r\n\r\nout vec4 oColor;\r\n\r\nvec2 vec2mulvec2(vec2 a, vec2 b)\r\n{\r\n    return vec2(a.x * b.x - a.y * b.y, a.x * b.y + a.y * b.x);\r\n}\r\n\r\nfloat Arg(vec2 a)\r\n{\r\n    if (a.x > 0.0)\r\n      return atan(a.y / a.x);\r\n    if (a.x == 0.0)\r\n      return asin(1.0) * sign(a.y);\r\n    return atan(a.y / a.x) + acos(-1.0) * sign(a.y);\r\n}\r\n\r\nvec2 vec2pow(vec2 a, float b)\r\n{\r\n    float fi = Arg(a);\r\n\r\n    return vec2(cos(fi * b), sin(fi * b)) * pow(length(a), b);\r\n}\r\n\r\nfloat vec2rec(vec2 xy)\r\n{\r\n    vec2 z = xy;\r\n    float i;\r\n\r\n    while (length(z) < 2.0 && i < 500.0)\r\n    {\r\n      i++;\r\n      z = vec2pow(z, range) + xy;\r\n    }\r\n\r\n    return i;\r\n}\r\n\r\nvoid main(void)\r\n{\r\n    vec2 xy = vec2(gl_FragCoord);\r\n    xy -= 500.0;\r\n    xy *= pow(10.0, transZ);\r\n    xy += 500.0;\r\n    xy.x += transX;\r\n    xy.y -= transY;\r\n    xy = xy / 1000.0;\r\n    xy.x -= 0.5;\r\n    xy.y -= 0.5;\r\n    float i = vec2rec(xy);\r\n\r\n    oColor = vec4(vec3(1.0 - ((i * 12.2324 / 13.7898) / 500.0 + 0.5), (i * 45.9766 / 54.7898) / 500.0 + 0.65, i / 500.0 + 0.5), 1.0);\r\n}\r\n";